.services {
	margin-top: 122px;
	&__wrap {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		position: relative;
		font-family: 'Oswald', sans-serif; }
	&__wrap h2 {
		position: absolute;
		top: 550px;
		left: -180px;
		font-size: 145px;
		letter-spacing: 5px;
		color: rgba(#000, 0.2);
		transform: rotate(-90deg); }
	&__block {
		position: relative;
		margin-bottom: 20px;
		transition: 0.5s; }
	&__block:hover {
		transform: scale(1.2); }
	&__block p {
		box-sizing: border-box;
		position: absolute;
		top: 10px;
		left: 0;
		width: 310px;
		padding: 10px;
		padding-bottom: 12px;
		padding-left: 30px;
		background: rgba(#000, 0.4);
		font-size: 18px;
		line-height: 22px;
		color: #fff; } }

@media (min-width: 900px) {
	.services {
		&__wrap {
			justify-content: space-between; }
		&__wrap h2 {
			top: 90px;
			left: -240px; } } }
