.footer {
	background-color: #ebebeb;
	margin-top: 66px;
	padding: 40px 0;
	& ul {
		list-style: none;
		padding: 0; }
	&__wrap {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin-bottom: 50px; }
	&__left-column {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center; }
	&__contacts {
		display: flex;
		flex-direction: column;
		justify-content: center;
		width: 353px;
		padding: 20px 0;
		margin-left: 20px; }
	&__contacts p {
		color: #4f4f4f;
		margin: 0; }
	&__contacts a {
		color: #4f4f4f; }
	&__contacts a:hover {
		color: #cd3a0d; }
	&__contacts span {
		font-weight: bold; } }

.nav__list {
	display: flex;
	justify-content: space-between;
	width: 280px; }
.nav__item {
	position: relative; }
.nav__item-service {
	display: none; }
.nav__item-service a {
	display: flex;
	padding: 10px; }
.nav__item-service a:hover {
	background: #ebebeb; }
.nav__item:hover .nav__item-service {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 550px;
	position: absolute;
	top: -220px;
	left: -130px;
	background: #fff;
	border-radius: 10px;
	list-style: none;
	padding: 0;
	z-index: 90; }
.nav__item a {
	color: #000; }
.nav__item a:hover {
	color: #cd3a0d; }
.social__list {
	margin-top: 10px;
	display: flex;
	justify-content: space-between;
	width: 190px; }
.social__item:hover {
	opacity: 0.7; }
@media (min-width: 700px) {
	.footer {
		&__wrap {
			flex-direction: row;
			justify-content: space-between;
			align-items: flex-start; }
		&__left-column {
			align-items: flex-start; } } }
