.header {
	z-index: 100;
	position: relative;
	&__block {
		position: absolute;
		top: 0;
		left: 0;
		display: flex;
		align-items: center;
		padding-top: 19px; }
	&__list {
		display: flex;
		justify-content: space-between;
		width: 300px;
		margin-top: 6px;
		list-style: none;
		padding: 0; }
	&__item {
		position: relative; }
	&__item-service {
		display: none; }
	&__item-service a {
		display: flex;
		padding: 10px; }
	&__item-service a:hover {
		color: #cd3a0d;
		background: #ebebeb; }
	&__item a {
		color: #000; }
	&__item:nth-child(2)::before {
		content: '>';
		position: absolute;
		bottom: -4px;
		right: -22px;
		width: 20px;
		height: 20px;
		transform: rotate(90deg); }
	&__item a:hover {
		color: #cd3a0d; }
	&__item:hover .header__item-service {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 550px;
		position: absolute;
		top: 25px;
		left: -130px;
		background: #fff;
		border-radius: 10px;
		list-style: none;
		padding: 0;
		z-index: 90; }
	&__item:last-child {
		margin-left: 22px; }
	&__wrap-tel {
		display: flex;
		justify-content: space-between;
		align-items: center;
		// width: 410px
		margin-top: -6px;
		margin-left: 20px;
		text-align: center; }
	&__tel {
		display: none;
		padding-left: 35px;
		font-size: 18px;
		color: #fff;
		background: url(../img/icon-call.png) no-repeat left; }
	&__tel:hover {
		color: #cd3a0d; }
	&__btn {
		padding: 15px 25px;
		color: #fff;
		background: #cd3a0d;
		border-radius: 5px; }
	&__btn:hover {
		background: #d16848; } }

@media (min-width: 700px) {
	.header {
		width: 600px;
		&__wrap-tel {
			width: 410px; } } }

@media (min-width: 800px) {
	.header {
		width: 700px;
		&__wrap-tel {
			margin-left: 60px; } } }
@media (min-width: 900px) {
	.header {
		width: 800px;
		&__list {
			margin-left: 20px; }
		&__wrap-tel {
			margin-left: 80px; }
		&__tel {
			display: block; } } }

@media (min-width: 1200px) {
	.header {
		width: 1132px;
		&__list {
			margin-left: 130px; }
		&__wrap-tel {
			margin-left: 200px; }
		&__block {
			left: 0px; } } }
