.about-work {
	display: none;
	background: #fff;
	width: 0%;
	height: 1922px;
	margin-left: 0%;
	&__dog {
		background: none; }
	&__blue-block {
		height: 770px;
		background: #004b9a;
		box-shadow: 10px 0px 100px #000; }
	&__title {
		position: relative;
		font-family: 'Oswald', sans-serif;
		font-size: 25px;
		line-height: 24px;
		letter-spacing: 1.5px;
		margin-left: 40px;
		margin-top: 155px;
		width: 220px; }
	&__title::after {
		content: '';
		position: absolute;
		bottom: -15px;
		left: 0;
		width: 100%;
		height: 40px;
		background: rgba(#d76a33, 0.3); }
	&__wrap {
		position: relative;
		margin-top: 100px;
		margin-left: 40px; }
	&__wrap iframe {
		border-radius: 50%;
		width: 230px;
		height: 230px; } }

#curved {
	position: absolute;
	top: -40px;
	left: 75px;
	transform: rotate(-15deg);
	font-size: 16px;
	color: #003399;
	text-transform: uppercase; }
#curved:hover {
	color: #cd3a0d; }


@media (min-width: 900px) {
	.about-work {
		display: block;
		width: 44%;
		height: 1922px;
		margin-left: -44%;
		&__dog {
			position: absolute;
			top: 120px;
			right: 0%;
			width: 500px;
			height: 1000px;
			background: url(../img/dog.png) no-repeat;
			background-size: 90%; }
		&__title {
			position: relative;
			font-family: 'Oswald', sans-serif;
			font-size: 40px;
			line-height: 60px;
			letter-spacing: 1.5px;
			margin-left: 45px;
			margin-top: 155px;
			width: 320px; }
		&__wrap {
			position: relative;
			margin-top: 180px;
			margin-left: 80px; }
		&__wrap iframe {
			border-radius: 50%;
			width: 230px;
			height: 230px; } } }
@media (min-width: 1200px) {
	.about-work {
		display: block;
		width: 44%;
		height: 1922px;
		&__dog {
			right: 5%;
			width: 550px;
			background-size: 100%; }
		&__wrap {
			margin-left: 180px; }
		&__title {
			font-size: 50px;
			margin-left: 95px;
			margin-top: 155px;
			width: 390px; } } }
