.advantages {
	position: absolute;
	z-index: 99;
	&__wrap {
		padding: 20px;
		background: #fff; }
	&__list {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		list-style: none;
		padding: 0; }
	&__item {
		padding-left: 0px;
		margin-top: 10px;
		width: 403px; }
	&__item:first-child {
		margin-left: 0px;
		background: none; }
	&__item:nth-child(2) {
		margin-left: 0px;
		background: none; }
	&__item:last-child {
		margin-left: 0px;
		background: none; }
	&__item h3 {
		margin: 0;
		margin-top: -8px;
		margin-bottom: 3px;
		font-size: 13px;
		font-weight: bold; }
	&__item p {
		margin: 0;
		font-size: 12px;
		color: #757575;
		line-height: 14px; } }

@media (min-width: 700px) {
	.advantages {
		&__list {
			display: flex;
			flex-direction: row; } } }
@media (min-width: 900px) {
	.advantages {
		&__item {
			padding-left: 80px;
			margin-top: 10px;
			width: 213px; }
		&__item:first-child {
			margin-left: 20px;
			background: url(../img/icon-1.png) no-repeat; }
		&__item:nth-child(2) {
			margin-left: 78px;
			background: url(../img/icon-2.png) no-repeat; }
		&__item:last-child {
			margin-left: 90px;
			background: url(../img/icon-3.png) no-repeat; } } }
@media (min-width: 1200px) {
	.advantages {
		&__item {} } }
