.title {
	padding-top: 88px;
	& h3 {
		font-size: 20px;
		color: #1f2837; }
	& h1 {
		margin-top: 37px;
		margin-bottom: 50px;
		font-family: 'Oswald', sans-serif;
		font-size: 38px;
		line-height: 60px;
		letter-spacing: 1px; }
	&__wrap {
		margin-top: 430px; }
	&__text {
		width: 360px;
		font-size: 18px;
		line-height: 23px; }
	&__image {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		flex-wrap: wrap;
		width: 390px;
		margin-top: 85px; }
	&__image img {
		margin-top: 20px;
		border: 1px solid #fff;
		box-shadow: 0px 0px 40px -10px #000; }
	&__image p {
		width: 230px;
		color: #fff;
		font-size: 16px;
		line-height: 20px; } }

@media (min-width: 700px) {
	.title {
		padding-top: 128px;
		& h1 {
			margin-bottom: 90px; }
		&__image {
			width: 590px; } } }

@media (min-width: 900px) {
	.title {
		padding-top: 158px;
		& h1 {
			margin-bottom: 110px; }
		&__text {
			width: 460px; } } }
@media (min-width: 1200px) {
	.title {
		padding-top: 198px;
		& h3 {
			font-size: 25px; }
		& h1 {
			margin-bottom: 130px;
			font-size: 50px;
			line-height: 60px;
			letter-spacing: 1.5px; }
		&__image {
			flex-direction: row; } } }
