.container {
	width: auto;
	margin: 0 20px; }

@media (min-width: 700px) {
	.container {
		width: 600px;
		margin: 0 auto; } }

@media (min-width: 800px) {
	.container {
		width: 700px;
		margin: 0 auto; } }

@media (min-width: 900px) {
	.container {
		width: 800px;
		margin: 0 auto; } }

@media (min-width: 1200px) {
	.container {
		width: 1132px;
		margin: 0 auto; } }
