.partners {
	margin-top: 81px;
	&__wrap {}
	&__wrap h2 {
		font-size: 45px;
		font-family: 'Oswald', sans-serif;
		letter-spacing: 1.5px; }
	&__sliders {
		width: 300px;
		margin: 0 auto;
		margin-top: 125px; }
	&__items {
		display: flex; } }

.owl-stage {
	display: flex;
	align-items: center; }
.owl-prev {
	position: absolute;
	top: calc(50% - 55px);
	left: -100px;
	width: 70px;
	height: 90px; }
.owl-next {
	position: absolute;
	top: calc(50% - 55px);
	right: -100px;
	width: 70px;
	height: 90px; }
.owl-prev:before,
.owl-next:before {
	content: '';
	position: absolute;
	top: calc(50% - 20px);
	left: calc(50% - 10px);
	width: 30px;
	height: 50px; }
.owl-prev:before {
	background: url(../img/prev.png) no-repeat; }
.owl-next:before {
	background: url(../img/next.png) no-repeat; }

@media (min-width: 900px) {
	.partners {
		&__sliders {
			width: 700px; } } }
@media (min-width: 1000px) {
	.partners {
		&__sliders {
			width: 800px; } } }
@media (min-width: 1100px) {
	.partners {
		&__sliders {
			width: 900px; } } }
@media (min-width: 1200px) {
	.partners {
		&__sliders {
			width: 1000px; } } }
